.transfer-model{padding:0 20px}.transfer-model .select-list-box{width:220px;float:left;border:1px solid #dcdee2;border-radius:3px}.transfer-model .select-list-box .tf-header{height:36px;line-height:36px;background:#f9fafc;position:relative;border-bottom:1px solid #dcdee2;margin-bottom:8px}.transfer-model .select-list-box .tf-header .title{position:absolute;left:8px;top:0}.transfer-model .select-list-box .tf-header .count{position:absolute;right:8px;top:0;font-size:12px}.transfer-model .select-list-box .tf-search{background:#fff;padding:8px}.transfer-model .select-list-box .tf-search .fa-search{color:#999}.transfer-model .select-list-box .tf-content{height:280px}.transfer-model .select-list-box .tf-content ul li{height:28px;line-height:28px;cursor:pointer}.transfer-model .select-list-box .tf-content ul li span{padding-left:10px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;width:210px;display:inline-block}.transfer-model .select-list-box .tf-content ul li:hover{background:#f6faff}.transfer-model .select-oper-box{width:20px;float:left}